<template>
  <section class="container-fluid searchAddress">
    <div class="searchArea">
      <!--:
      v-model,;
      @focus ;
      @click 
      -->
      <el-input
        v-model="input"
        @focus="showIcon"
        placeholder="//"
      ><i slot="suffix" class="el-input__icon el-icon-close" @click="clearInput" v-if="isIcon"></i></el-input>
      <el-button
        type="primary"
        @click="searchInfo(input)" :disabled="!input"></el-button>
    </div>
    <ul class="recordList" v-if="record">
      <li></li>
      <li v-for = "(item ,key) in searchList" class="records" @click.self="selectInRecord(item)">{{item}}</li>
      <li @click="clearAllRecord"></li>
    </ul>
    <ul class="searchFail" v-if="noAnswer">
      <li>!</li>
    </ul>
    <ul class="searchFail" v-if="isAnswer">
      <li v-for="(item ,key) in arrData" @click="selectThis(item)">
        <p>{{item.name}}</p>
        <p>{{item.address}}</p>
      </li>
    </ul>
  </section>
</template>

<script>
import Vue from 'vue'
// 
export default {
  name: "searchAddress",
  data() {
    return {
      // 
      isIcon:false,
      // 
      noAnswer:false,
      // ,
      isAnswer:false,
      // ,
      record:true,
      input: '',
      // 
      searchList:[],
      // store
      curCity:{},
      // 
      arrData:[],
      // 
      selectAddress:''
    }
  },
  methods: {
    //  ,
    clearInput(){
      this.input = "";
      this.isIcon = false;
    },
    // ,
    showIcon(){
      this.isIcon = true
    },
    // ,
    clearAllRecord(){
      this.storage.set("searchList", []);
      this.record = false
    },
    // ,
    removeData(key){
      this.searchList.splice(key,1);
      this.storage.set("searchList",this.searchList);
      this.searchList.checked = false;
    },
    // 
    selectThis(i){
      this.selectAddress = i.name;
      // ,
      this.$router.push({name:'addAddress',params:{select:this.selectAddress}})
    },
    // 
    selectInRecord(i){
      this.searchInfo(i)
    },
    searchInfo(i){
      //
      this.input = i;
      // 
      this.doAdd(i);
      //
      this.$store.commit({
        type:'amendDataLoad'
      });
      // ,,
      Vue.axios.get(`https://elm.cangdu.org/v1/pois?city_id=${this.curCity.id}&keyword=${i}&type=search`,null).then(res => {
        //
        this.$store.commit({
          type:'amendDataLoad'
        });
        // 
        this.isAnswer = true;
        this.record = false;
        this.noAnswer = false;
        console.log(res.data);
        this.arrData = res.data;
        // 
        if(res.data === []){
          this.record = false;
          this.noAnswer = true;
          this.isAnswer = false
        }
      }).catch((error) => {
        console.log(error);
        // ,
        this.record = false;
        this.noAnswer = true;
        this.isAnswer = false
      })
    },
    doAdd() {
      // 
      this.input = this.input.trim();
      // list
      if (this.searchList.length>0){//
        // 
        if(this.searchList.includes(this.input)){
          // ,
          this.searchList.splice(this.searchList.indexOf(this.input),1,this.input);
        }else{
          // ,
          this.searchList.unshift(this.input);
        }
      }else{
        // ,
        this.searchList.unshift(this.input);
      }

      // 6,
      if(this.searchList.length>20){
        this.searchList.pop()
      }
      this.storage.set("searchList", this.searchList)
    },

  },
  mounted(){
    /* vue*/
    let searchList = this.storage.get("searchList");
    if (searchList){
      this.searchList = searchList;
    }
  },
  beforeRouteEnter(to,from,next){
    next(vm=>{
      vm.curCity = vm.$store.state.ghc.currentCity
    })
  }
}
</script>

<style scoped>
  .searchAddress{
    width: 100%;
    height: 100%;
    padding: 0;
  }
  .searchArea{
    display: inline-block;
    width: 100%;
    height: 0.6rem;
    line-height: 0.6rem;
    background-color: white;
    padding: 0 0.1rem;
  }
  .el-input{
    width: 2.7rem;
    float: left;
  }
  .el-input__inner{
    outline-style: none;
  }
  .el-row{
    width: 0.8rem;
  }
  .el-button--primary{
    width: 0.8rem;
    float: right;
    margin-top: 0.1rem;
  }
  .el-input__icon{
    color: darkslateblue;
    font-size: 0.16rem;
    font-weight: bolder;
  }
  .recordList{
    background-color: white;
    margin-bottom: 0;
  }
  .recordList>li{
    border-bottom: 0.01rem solid #e4e4e4;
    height: 0.4rem;
    line-height: 0.4rem;
    padding: 0 0.1rem;
  }
  .recordList>li:nth-child(1){
    background-color: #f5f5f5;
  }
  .recordList>li:last-child{
    text-align: center;
    color: #008de1;
    border-bottom: none;
  }
  .records>i{
    float: right;
  }
  .searchFail>li{
    text-align: left;
    padding: 0.04rem 0.14rem;
    color: #000;
    height: 0.6rem;
    background-color: white;
    margin-top: 0.02rem;
  }
  .searchFail>li>p{
    height: 0.2rem;
    /*word-break: keep-all;*/
    /*white-space: nowrap;*/
    overflow: hidden;
    /*text-overflow: ellipsis;*/
  }
</style>



