<template>
  <div id="domeShoppingCartComponent">
    <!---->
    <transition name="dome3">
      <div class="dome-shade" v-if="draw" @click.self="cartArr[0]?draw=!draw:null"></div>
    </transition>
    <div id="domeBuyCart">
      <!---->
      <transition name="dome2">
        <!--,-->
        <div class="dome-box" v-if="draw" >
          <!---->
          <div class="dome-cart-box">
            <!---->
            <span class="dome-cart-text left"></span>
            <!---->
            <span class="dome-empty right" @click="domeEmpty"><i class="iconfont">&#xe626;</i> </span>
          </div>
          <div></div>
          <!---->
          <div>
            <div class="dome-commodity-box" v-for="(item, index) in cartArr" :key="index">
              <!---->
              <div class="dome-commodity">
                <span>{{item.name}}</span>
              </div>
              <!---->
              <div class="dome-univalence">
                <span>{{item.price}}</span>
              </div>
              <!---->
              <div class="dome-plus-minus">
                <!---->
                <i class="iconfont" @click="domeDelete(item)">&#xe605;</i>
                <!---->
                <span>{{item.dome}}</span>
                <!---->
                <i class="iconfont" @click="domeAdd(item)">&#xe635;</i>
              </div>
            </div>
          </div>
        </div>
      </transition>
      <!---->
      <div class="dome-foot" @click.self="cartArr[0]?draw=!draw:null">
        <!---->
        <!--<transition  name="dome">-->
          <div class="dome-cart left" :class="{domeBackgroundColor:cartArr[0],suofang:addZ1}"@click="cartArr[0]?draw=!draw:null">
          <!---->
          <span class="classifyCount" v-if="cartArr[0]">{{cartAmount}}</span>
          <!--i-->
          <i class="iconfont dome-cart-i" >&#xe64f;</i>
        </div>
        <!--</transition>-->
        <!---->
        <div class="dome-price-freight" @click="cartArr[0]?draw=!draw:null">
          <!---->
          <p class="dome-price"> {{totalPrices?totalPrices:'00'}}.00</p>
          <!---->
          <p class="dome-freight">{{domeCarriage}}</p>
        </div>
        <!---->
        <span class="dome-affirm" :class="{domeBackgroundColorRight:cartArr[0]}" @click="goPayment">{{cartArr[0]?``:`${'20'}`}}</span>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: "buyCart",
  data () {
    return {
      // 
      draw:false,
      totalPrices:0,
      foodsArr:[],
      cartAmount:0,
      addZ1:'',
      // 
      cartArr:[],
      // 
      domeCarriage:0
    }
  },
  computed: {
    arr () {
      // 
      return this.$store.state.dome.allCartSingleFood
    },
    addZ () {
      return this.$store.state.dome.addZ
    }
  },
  watch:{
    addZ: {
      handler() {
        this.addZ1 = this.$store.state.dome.addZ
      },
      //,false
      immediate:true,
      //
      deep:true
    },
    arr: {
      handler() {
        let arr = []
        for (let obj of this.$store.state.dome.allCartSingleFood) {
          for (let obj1 of obj.foods) {
            for (let obj2 of obj1.specfoods) {
              if (obj2.dome !== 0){
                arr.push(obj2)
              }
            }
          }
        }
        this.cartArr = arr
        console.log('this.cartArr',this.cartArr)
        this.domeCarriage = this.$store.state.dome.singleStore.float_delivery_fee
        this.cartAmount = 0
        this.totalPrices = 0
        for (let prices of this.cartArr) {
          this.cartAmount += prices.dome
          this.totalPrices += prices.price * prices.dome
        }
      },
      //,false
      immediate:true,
      //
      deep:true
    }
  },
  methods: {
    // 
    domeEmpty () {
      this.draw = false
      this.$store.commit({type:'emptySingleFood'})
      this.totalPrices = '00'
    },
    // 
    domeAdd (i) {
      console.log('i',i)
      // i.dome++
      this.$store.commit({type:'cartAddSingleFood',data:i})
      // console.log('singleStore',this.$store.state.dome.singleStore)
    },
    // 
    domeDelete (i) {
      // i.dome--
      this.$store.commit({type:'cartDeleteSingleFood',data:i})
      console.log('this.cartArr[0]',this.cartArr)
      if (this.cartArr.length === 1 && this.cartArr[0].dome === 0) {
        console.log('')
        this.draw = false
        this.totalPrices = '00'
      }
    },
    // 
    goPayment () {
      let id = this.$store.state.dome.singleStore.id
      let geohash = this.$store.state.dome.singleStore.location
      console.log('id',typeof id)
      // console.log(this.$store.state.dome.)
      if (this.cartArr[0]) {
        // 
        let arr = []
        for (let obj of this.$store.state.dome.cartSingleFood){
          let objCart = {attrs:[], extra:{}, id:obj.entities.id, name:obj.entities.name, packing_fee:obj.entities.packing_fee, price:obj.entities.price, quantity:obj.quantity, sku_id:obj.entities.sku_id, specs:obj.entities.specs, stock:obj.entities.stock}
          arr.push(objCart)
        }
        console.log('arr1111111111111',arr)
        this.$http({
          method:"POST",
          url:'https://elm.cangdu.org/v1/carts/checkout',
          withCredentials: true, // 
          data:{
            restaurant_id:id,
            geohash: geohash[0]+','+geohash[1],
            entities: [{id:5234}]
          }
        }).then(res => {
          console.log('',res)
        })
        this.$router.push({name:'confirmOrder'})
      }
    }
  }
}
</script>

<style scoped>
/**/
#domeShoppingCartComponent {
  width: 100%;
  height: 100%;
}
/**/
#domeBuyCart {
  color: white;
  display: flex;
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 0.50rem;
  /*background-color: black;*/
  /*z-index: 55;*/
}
/**/
.dome-foot {
  width: 100%;
  height: 0.50rem;
  color: white;
  display: flex;
  position: fixed;
  bottom: 0;
  background-color: black;
}
/**/
.dome-cart {
  position: absolute;
  top: -0.2rem;
  margin-left: 0.2rem;
  background-color: black;
  width: 0.5rem;
  height: 0.5rem;
  border-radius: 100%;
  border: #1e1e1e 0.05rem solid;
  text-align: center;
}
/**/
.domeBackgroundColor {
  background-color: #008de1;
}
.domeBackgroundColorRight {
  background-color: #18ff21 !important;
}
/**/
.dome-price {
  font-size: 0.2rem;
  /*letter-spacing:0.1rem;*/
  margin: 0;
  line-height: 0.25rem;
}
/**/
.dome-freight {
  font-size: 0.12rem;
  margin: 0;
  line-height: 0.25rem;
}
/*i*/
.dome-cart-i {
  font-size: 0.3rem;
  line-height: 0.4rem;
}
/**/
.dome-price-freight {
  position: absolute;
  left: 1rem;
}
/**/
.dome-affirm {
  position: absolute;
  right: 0;
  width: 1.3rem;
  height: 0.5rem;
  background-color: #a4a4a4;
  text-align: center;
  font-size: 0.16rem;
  line-height: 0.5rem;
}
/*,*/
.dome-box {
  width: 102%;
  max-height: 4.5rem;
  background-color: white;
  position: fixed;
  bottom: 0.5rem;
  overflow-y: scroll;
  /*z-index: 3;*/
}
/**/
.dome-cart-box {
  position: fixed;
  width:100%;
  height: 0.5rem;
  background-color: #ddd;
  line-height: 0.5rem;
  z-index: 66;
}
/**/
.dome-cart-box+div {
  width:100%;
  height: 0.5rem;
}
/**/
.dome-cart-text {
  width: 1rem;
  text-align: center;
  height: 0.5rem;
  color: black;
}
/**/
.dome-empty {
  color: black;
  width: 1rem;
  line-height: 0.5rem;
}
/**/
.dome-commodity-box {
  /*position: relative;*/
  /*top: 0.5rem;*/
  width: 100%;
  height: 0.5rem;
  line-height: 0.5rem;
  display: flex;
  /*z-index: 4;*/
}
/**/
.dome-commodity {
  position: absolute;
  left: 0rem;
  width: 1rem;
  text-align: center;
  color: black;
  overflow: hidden;
  text-overflow:ellipsis
}
.dome-commodity>span {
  display: inline-block;
  height: 0.5rem;
  width: 1rem;
  word-break:keep-all;/*  */
  white-space:nowrap;/*  */
  overflow:hidden;/*  */
  text-overflow:ellipsis;/* (...) overflow:hidden;*/
}
/**/
.dome-univalence {
  position: absolute;
  right: 0.5rem;
  width: 1rem;
  text-align: center;
  color: red;
}
/**/
.dome-plus-minus {
  position: absolute;
  right: 0.1rem;
  width: 0.7rem;
  text-align: center;
  color: black;
}
/*i*/
.dome-plus-minus>i {
  color: blue;
}
/**/
.dome-shade {
  width: 100%;
  height: 100%;
  position: fixed;
  bottom: 0;
  background-color: rgba(33,33,33,0.5);
}
/**/
.classifyCount {
  /*display: inline-block;*/
  background-color: red;
  position: absolute;
  right: -0.05rem;
  color: white;
  top: -0.05rem;
  height: 0.16rem;
  line-height: 0.16rem;
  text-align: center;
  font-size: 0.12rem;
  padding:0 10%;
  border-radius: 50%;
  z-index: 11;
}
/**/
.dome2-enter-active, .dome2-leave-active {
  transition: bottom 1s;
}
.dome2-enter, .dome2-leave-to/* .fade-leave-active below version 2.1.8 */ {
  bottom: -5rem;
}
/*!**!*/
.dome3-enter-active, .dome3-leave-active {
  transition: background-color 0.5s;
}
.dome3-enter, .dome3-leave-to {
  background-color:  rgba(33,33,33,0);
}
  /**/
.suofang {
  animation: dome 1s;
}
@keyframes dome {
  0%{
    transform:scale(1,1);
  }
  50%{
    transform: scale(1.5,1.5);
  }
  100%{
    transform: scale(1,1);
  }
}
</style>



