<template>
  <section class="container-fluid addAddress">
    <div class="infos">
      <input type="text" placeholder="" v-model="yourName">
      <input type="text" placeholder="//" @click="searchAddress" v-model="yourAddres">
      <input type="text" placeholder="" v-model="yourDetailAddres">
      <input type="text" placeholder="" v-model="yourNumber">
      <input type="text" placeholder="()" v-model="phone_bk">
    </div>
    <button class="btn" @click="addNew"></button>

  </section>
</template>

<script>
// 
export default {
  name: "add",
  data(){
    return{
      // 
      userInfomation:{},
      // name
      yourName:'',
      // 
      yourNumber:'',
      // 
      phone_bk:'',
      // 
      yourAddres:'',
      // 
      yourDetailAddres:'',
      // 
      yourTag:'',
      // 
      geohash:'',
      // 
      newAddres:[]
    }
  },
  methods:{
    searchAddress(){
      // 
      this.$router.push({name:'addDetail'})
    },
    addNew(){
// ,
      if(this.yourNumber === ''|| this.yourName === ''||this.yourDetailAddres === ''){
        alert('');
        return
      }
      //,
      this.$http({
        method:'post',
        url:`https://elm.cangdu.org/v1/users/${this.userInfomation.user_id}/addresses`,
        withCredentials:true,
        data:{
          address: this.yourAddres,
          address_detail: this.yourDetailAddres,
          geohash: this.geohash,
          name: this.yourName,
          phone: this.yourNumber,
          tag: '',
          sex: 1,
          poi_type:0,
          phone_bk:this.phone_bk,
          tag_type:2
        }
      }).then(res => {
        // myAddress
        this.$router.push({name:'myaddress'});
        console.log('',res.data)
      }).catch((error) => {
        console.log(error);
      });
    }
  },
  mounted(){
    // vuex,id,geohash
    this.userInfomation = this.$store.state.ghc.userInfo;
    this.geohash = this.$store.state.ghc.localInfo.geohash;
    console.log(this.userInfomation,this.geohash);
    //
    console.log(this.$route.query.detailAdd);
    this.yourAddres = this.$route.query.detailAdd?this.$route.query.detailAdd.name:null;
    this.yourDetailAddres = this.$route.query.detailAdd?this.$route.query.detailAdd.address:null;

  }
}
</script>

<style scoped>
  .addAddress{
    width: 100%;
    height: 100%;
    padding: 0;
    background-color: #f5f5f5;
  }
  .infos{
    width: 100%;
    padding: 0.1rem;
    background-color: white;
    text-align: center;
  }
  .infos>input{
    outline-style: none;
    color: gray;
    width: 98%;
    height: 0.36rem;
    margin: 0.06rem 0;
    padding: 0 0.1rem;
    border: 0.01rem solid #e4e4e4;
    border-radius: 0.04rem;
    background-color: whitesmoke;
  }
  .btn{
    margin:0.2rem 5%;
    width: 90%;
    height: 0.4rem;
    color: lightgrey;
    background-color: limegreen;
    border-radius: 0.04rem;
    text-align: center;
  }
</style>



