<template>
  <section class="container-fluid AccountInfo">
    <div class="content">
      <div><span></span><i class="iconfont">&#xe634;</i><img src="../../../assets/logo.png" alt="!">
        <input type="file"/>
      </div>
      <router-link :to="{name:'setusername'}"><span></span><span>{{getName}}<i class="iconfont">&#xe634;</i></span></router-link>
      <router-link :to="{name:'myaddress'}"><span></span><i class="iconfont">&#xe634;</i></router-link>
      <p></p>
      <router-link :to="{}" @click.native="remindSet"><span><i class="iconfont">&#xe633;</i></span><i class="iconfont">&#xe634;</i></router-link>
      <p></p>
      <router-link :to="{name:'forget'}"><span></span><span><i class="iconfont">&#xe634;</i></span></router-link>
    </div>
    <div class="foot">
      <el-button class="btn" @click="remindClose"></el-button>
    </div>
    <!--APP-->
    <div
      v-if="popupVisible1"
      class="animated heartBeat alertBox1">
      <div class="remindSet">
        <i class="iconfont">&#xe632;</i>
        <p>APP</p>
        <button @click="sureSet"></button>
      </div>
    </div>
    <!---->
    <div
      v-if="popupVisible2"
      class="animated heartBeat alertBox2" >
      <div class="remindClose">
        <i class="iconfont">&#xe632;</i>
        <p></p>
        <button @click="waiting"></button>
        <button @click="closeIt"></button>
      </div>
    </div>
  </section>
</template>

<script>
  import Vue from 'vue'
  // 
  import { Popup } from 'mint-ui';
  Vue.component(Popup.name, Popup);
// 
export default {
  name: "info",
  data(){
    return{
      popupVisible1:false,
      popupVisible2:false,
      // 
      getName:this.storage.get('username'),
      // 
      pics:'../../../assets/logo.png',
    }
  },
  methods:{
    remindSet(){
      // ,
      this.popupVisible1 = true
    },
    remindClose(){
      // ,
      this.popupVisible2 = true;

    },
    sureSet(){
      // ,
      this.popupVisible1 = false
    },
    waiting(){
      // ,
      this.popupVisible2 = false
    },
    closeIt(){
      //
      this.$store.commit({
        type:'amendDataLoad'
      });
      // ,
      Vue.axios.get('https://elm.cangdu.org/v2/signout',null).then(res => {
        //
        this.$store.commit({
          type:'amendDataLoad'
        });
        console.log(res.data);
        // ,
        this.popupVisible2 = false;
        // ,
        this.$router.push({name:'mind'});
        // 
        this.$store.commit({type:'getLoggingStatus',loggingStatus:false})
      }).catch((error) => {
        console.log(error)
      })
    },
  },
 }
</script>

<style scoped>
  .AccountInfo{
  width: 100%;
  height: 100%;
  padding: 0;
  position: relative;
}
.content>a,p{
  width: 100%;
  height: 0.4rem;
  line-height: 0.4rem;
  text-decoration: none;
  background-color: white;
  border-top: 0.01rem solid #e4e4e4;
  padding: 0 0.1rem;
  clear: both;
  display: inline-block;
  color: #000;
}
.content>div{
  display: inline-block;
  width: 100%;
  height: 0.8rem;
  line-height: 0.8rem;
  background-color: white;
  border-top: 0.01rem solid #e4e4e4;
  padding: 0 0.1rem;
  clear: both;
  color: #000;
}
.content>p{
  height: 0.3rem;
  line-height: 0.3rem;
  background-color: #f5f5f5;
  margin-bottom: 0;
  font-size: 0.12rem;
  color: dimgrey;
}
.content>a>span+span{
  float: right;
}
.content>div:nth-child(1){
  position: relative;
}
.content>div:nth-child(1)>i{
  float: right;
}
.content>div:nth-child(1)>input{
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 1000;
  margin: 0;
  left: 0;
  top: 0;
}
.content>div:nth-child(1)>img{
  float: right;
  width: 0.6rem;
  margin: 0.1rem;
  border-radius: 0.6rem;
  background-color: #008de1;
}
.content>a:nth-child(3)>i{
  float: right;
}
.content>a:nth-child(5)>i{
  float: right;
}
.content>a:nth-child(5)>span{
  width: 0.4rem;
  height: 0.4rem;
  color: #fff;
  background-color: #008de1;
  border-radius: 0.04rem;
  margin-right: 0.1rem;
}
.content>a:nth-child(1){
  margin-top: 0.1rem;
  height: 0.8rem;
  line-height: 0.8rem;
}
.content>a:nth-child(4){
  background-color: #f5f5f5;
}
.content>a:nth-child(6){
  background-color: #f5f5f5;
}
.content>a:last-child{
  border-bottom: 0.01rem solid #e4e4e4;
}
.foot{
  text-align: center;
}
.btn{
  width: 3.6rem;
  margin: 0.4rem auto;
  text-align: center;
  height: 0.4rem;
  color: white;
  background-color: red;
  border-radius: 0.04rem;
}
.alertBox1,.alertBox2{
  position: fixed;
  left: 15%;
  top: 20%;
  width: 70%;
  height: 2rem;
  border-radius: 0.1rem;
  background-color: white;
}
.alertBox2{
  height: 2.4rem;
  text-align: center;
}
.remindSet, .remindClose{
  text-align: center;
  position: relative;
  height: 2rem;
}
  .remindSet>i{
    font-size: 0.8rem;
    color: orange;
  }
  .remindClose>i{
    font-size: 1rem;
    color: orange;
  }
.remindSet>p{
  border: 0;
}
.remindClose>p{
  border: 0;
  font-size: 0.2rem;
}
.remindSet>button{
  width: 100%;
  height: 0.4rem;
  border: 0;
  margin-bottom: 0;
  padding: 0;
  background-color: limegreen;
  color: white;
  position: absolute;
  left: 0;
  bottom: 0;
  border-radius:0 0 0.1rem 0.1rem ;
}
  .remindClose>button{
    width: 30%;
    height: 0.4rem;
    border: none;
    padding: 0;
    background-color: silver;
    color: white;
    border-radius: 0.06rem;
    margin:0 0.1rem ;
  }
.remindClose>button+button{
  background-color: red;
}
</style>



