<script>
// 
// Vue,Popup
import Vue from 'vue'
import { Popup } from 'mint-ui';
// Popup,
Vue.component(Popup.name, Popup);
export default {
  name: "service",
  data(){
    return{
      //
      popupVisible1:false,
      popupVisible2:false,
      // 
      isIf:true,
      // 
      questionTitle:[]
    }
  },
  methods:{
    call(){
      // ,
      this.popupVisible1 = true
    },
    addPerson(){
      this.popupVisible1 = false;
      this.popupVisible2 = true
    }
  },
  mounted(){
    //
    this.$store.commit({
      type:'amendDataLoad'
    });
    Vue.axios.get(`https://elm.cangdu.org/v3/profile/explain`,null).then(res => {
      // 
      this.$store.commit({
        type:'amendDataLoad'
      });
      // ,
      this.questionTitle = Object.entries(res.data);
    }).catch((error) => {
      console.log(error)
    })
  }
}
</script>

<style scoped>
  /*:<mt-popup></mt-popup>viewdivclass="mint-popup"*/
.mint-popup{
  width: 3rem;
  height: 1.8rem;
  border-radius: 0.1rem;
}
  .service{
    background-color: white;
  }
.contact .mint-popup+.mint-popup{
  height: 1.2rem;
}
.call>a{
  color: black;
  display: block;
  text-decoration: none;
  margin: 0;
  width: 3rem;
  height: 0.6rem;
  padding-left: 0.2rem;
  line-height: 0.6rem;
  border-bottom: 0.01rem solid #e4e4e4;
}
.call>a:nth-child(3){
  border-bottom: none;
}
  .addPerson>a{
    color: black;
    display: block;
    text-decoration: none;
    margin: 0;
    width: 3rem;
    height: 0.6rem;
    padding-left: 0.2rem;
    line-height: 0.6rem;
    border-bottom: 0.01rem solid #e4e4e4;
  }
.addPerson>a+a{
  border-bottom: none;
}
.left,.right{
    height: 1rem;
    text-align: center;
    line-height: 0.26rem;
    display: inline-block;
    padding: 0.3rem 0;
    border-bottom: 0.01rem solid #e4e4e4;
}
.left{
  border-right: 0.01rem solid #e4e4e4;
}
.left>a,.right>a{
  text-decoration: none;
  color: black;
}
  .left>a>i{
    font-size: x-large;
    color: orange;
  }
  .right>a>i{
    font-size: x-large;
    color: limegreen;
  }
.questionTitle>p{
  font-size: 0.16rem;
  float: left;
  width: 100%;
  height: 0.8rem;
  line-height: 0.8rem;
  border-bottom: 0.01rem solid #e4e4e4;
  padding-left:0.16rem ;
}
.questionTitle p{
  margin: 0;
}
.questionTitle>div>a{
  height: 0.4rem;
  line-height: 0.4rem;
  text-decoration: none;
  color: dimgrey;
  display: block;
  clear: both;
  border-bottom: 0.01rem solid #e4e4e4;
  padding:0 0.16rem ;
}
  .questionTitle>div>a>p,i{
    display: inline-block;
  }
  .questionTitle>div>a>p{
    float: left;
  }
  .questionTitle>div>a>i{
    float: right;
  }
</style>



