import Vue from 'vue'
import Router from 'vue-router'
import axios from 'axios'
import VueAxios from 'vue-axios'
// 
import City from '../page/city/city'
// 
import Balance from '../page/balance/balance'
// 
import mybalance from '../page/balance/children/mybalance'
// 
import BalanceDetail from '../page/balance/children/detail'
// 
import Benefit from '../page/benefit/benefit'
// 
import BenefitCommend from '../page/benefit/children/commend'
// 
import BenefitCoupon from '../page/benefit/children/coupon'
// 
import BenefitExchange from '../page/benefit/children/exchange'
// 
import BenefitHbDescription from '../page/benefit/children/hbDescription'
// 
import BenefitHbHistory from '../page/benefit/children/hbHistory'
// 
import confirmOrder from '../page/confirmOrder/confirmOrder'
// 
import chooseAddress from '../page/confirmOrder/chooseAddress'
// 
import invoice from '../page/confirmOrder/invoice'
// 
import payment from '../page/confirmOrder/payment'
// 
import remark from '../page/confirmOrder/remark'
// 
import userValidation from '../page/confirmOrder/useValidation'
// 
import addAddress from '../page/confirmOrder/addAddress'
// 
import searchAddress from '../page/confirmOrder/searchAddress'
// App
import download from '../page/download/downLoad'
// 
import find from '../page/find/find'
// 
import food from '../page/food/food'
// ,
import forget from '../page/forget/forget'
// 
import home from '../page/home/home'
// 
import login from '../page/login/login'
// 
import msite from '../page/msite/msite'
// 
import order from '../page/order/order'
// 
import orderDetail from '../page/order/children/orderDetail'
// 
import points from '../page/points/points'
// 
import mypoints from '../page/points/children/mypoints'
// 
import pointsDetail from '../page/points/children/detail'
// 
import profile from '../page/profile/profile'
// 
import mind from '../page/profile/children/mind'
// 
import info from '../page/profile/children/info'
// 
import setusername from '../page/profile/children/setusername'
// 
import myaddress from '../page/profile/children/myaddress'
// 
import add from '../page/profile/children/add'
// 
import addDetail from '../page/profile/children/addDetail'
// 
import search from '../page/search/search'
// 
import service from '../page/service/service'
// 
import questionDetail from '../page/service/children/questionDetail'
// 
import shop from '../page/shop/shop'
// 
import foodDetail from '../page/shop/children/foodDetail'
// 
import shopDetail from '../page/shop/children/shopDetail'
//
import singleFoodDetail from '../page/shop/children/children/singleFoodDetail'
// 
import shopSafe from '../page/shop/children/children/shopSafe'
// 
import vipcard from '../page/vipcard/vipcard'
// 
import invoiceRecord from '../page/vipcard/invoiceRecord'
// 
import useCart from '../page/vipcard/useCart'
// 
import vipDescription from '../page/vipcard/vipDescription'
//
import computeTime from '../components/common/computeTime'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      // 
      path: '/city',
      component: City,
      name: 'city'
    },
    { //  
      path: '/balance',
      redirect:'/balance/mybalance',
      component: Balance,
      name: 'balance',
      children: [ // 
        // 
        {
          path:'mybalance',
          component:mybalance,
          name:'mybalance'
        },
        // 
        {
          path: 'detail',
          component: BalanceDetail,
          name: 'BalanceDetail'
        }
      ]
    },
    { //  
      path: '/benefit',
      component: Benefit,
      name: 'benefit',
      children: [ // 
        {
          // 
          path: 'commend',
          component: BenefitCommend,
          name: 'commend'
        },
        {
          // 
          path: 'coupon',
          component: BenefitCoupon,
          name: 'coupon'
        },
        {
          // 
          path: 'exchange',
          component: BenefitExchange,
          name: 'exchange'
        },
        {
          // 
          path: 'hbDescription',
          component: BenefitHbDescription,
          name: 'hbDescription'
        },
        {
          // 
          path: 'hbHistory',
          component: BenefitHbHistory,
          name: 'hbHistory'
        }
      ]
    },
    { //  
      path: '/confirmOrder',
      component: confirmOrder,
      name: 'confirmOrder',
    },
    {
      // 
      path: '/chooseAddress',
      component:chooseAddress,
      name: 'chooseAddress'
    },
    {
      // 
      path: '/chooseAddress',
      component: chooseAddress,
      name: 'chooseAddress'
    },
    {
      // 
      path: '/invoice',
      component: invoice,
      name: 'invoice'
    },
    {
      // 
      path: '/payment',
      component: payment,
      name: 'payment'
    },
    {
      // 
      path: '/remark',
      component: remark,
      name: 'remark'
    },
    {
      // 
      path: '/userValidation',
      component: userValidation,
      name: 'userValidation'
    },
    {
      // 
      path: '/addAddress',
      component: addAddress,
      name: 'addAddress',
    },
    {
      // 
      path: '/searchAddress',
      component: searchAddress,
      name: 'searchAddress'
    },
    { // App 
      path: '/download',
      component: download,
      name: 'download'
    },
    { //  
      path: '/find',
      component: find,
      name: 'find'
    },
    { //  
      path: '/food',
      component: food,
      name: 'food'
    },
    { // , 
      path: '/forget',
      component: forget,
      name: 'forget'
    },
    { //  
      path: '/home',
      component: home,
      name: 'home'
    },
    { //  
      path: '/login',
      component: login,
      name: 'login'
    },
    { //  
      path: '/msite',
      component: msite,
      name: 'msite'
    },
    { //  
      path: '/computeTime',
      component: computeTime,
      name: 'computeTime'
    },
    { //  
      path: '/order',
      component: order,
      name: 'order',
      children: [ // 
        { // 
          path: 'orderDetail',
          component: orderDetail,
          name: 'orderDetail'
        }
      ]
    },
    { //  
      path: '/points',
      redirect:'/points/mypoints',
      component: points,
      name: 'points',
      children: [ // 
        {
          // 
          path: 'mypoints',
          component: mypoints,
          name: 'mypoints'
        },
        { // 
          path: 'pointsDetail',
          component: pointsDetail,
          name: 'pointsDetail'
        }
      ]
    },
    { //  
      path: '/profile',
      component: profile,
      redirect:'/profile/mind',
      name: 'profile',
      children: [ // 
        {
          // 
          path: 'mind',
          component: mind,
          name: 'mind'
        },
        { // 
          path: 'info',
          component: info,
          name: 'info'
        },
        { // 
          path: 'setusername',
          component: setusername,
          name: 'setusername'
        },
        { // 
          path: 'myaddress',
          component: myaddress,
          name: 'myaddress',
        },
        { // 
          path: 'add',
          component: add,
          name: 'add',
        },
        { // 
          path: 'addDetail',
          component: addDetail,
          name: 'addDetail',
        }
      ]
    },
    { //  // 
      path: '/search',
      component: search,
      name: 'search'
    },
    { //  
      path: '/service',
      component: service,
      name: 'service',
      children: [ // 
        { // 
          path: 'questionDetail',
          component: questionDetail,
          name: 'questionDetail'
        }
      ]
    },
    { //  
      path: '/shop',
      component: shop,
      name: 'shop',
      redirect:'/shop/shopDetail',
      children: [ // 
        { //  
          path: 'shopDetail',
          component: shopDetail,
          name: 'shopDetail',
        }
      ]
    },
    {//  
    path: '/foodDetail',
    component: foodDetail,
    name: 'foodDetail',
    },
    { //  
      path: '/singleFoodDetail',
      component: singleFoodDetail,
      name: 'singleFoodDetail',
    },
    { //  
      path: '/shopSafe',
      component: shopSafe,
      name: 'shopSafe'
    },
    { //  
      path: '/vipcard',
      component: vipcard,
      name: 'vipcard',
    },
    { //  
      path: '/invoiceRecord',
      component: invoiceRecord,
      name: 'invoiceRecord'
     },
    { //  
      path: '/useCart',
      component: useCart,
      name: 'useCart'
    },
    { //  
      path: '/vipDescription',
      component: vipDescription,
      name: 'vipDescription'
    }
  ]
})



