<template>
  <section>
    <div class="box" v-if="isIf === ''|| isIf === ''">
      <span class="domeHb" v-if="isIf === ''"></span>
      <span class="domeVoucher" v-if="isIf === ''"></span>
      <span class="hB" :class="{pitchOn:isIf === ''}" @click="isIf = ''"></span>
      <span class="chit" :class="{pitchOn:isIf === ''}" @click="isIf = ''"></span>
    </div>
    <div v-if="isIf === ''">
      <div class="hbDiv">
        <span @click="isIf = hbDes">
          <router-link :to="{name: 'hbDescription', query:{move:hbDes}}" class="right"><i class="iconfont">&#xe63e;</i>{{hbDes}}</router-link>
        </span>
        <p><dome>{{hBarr.length}}</dome></p>
      </div>
      <div v-for="(item, index) in hBarr" class="hbBox" :key="index" :class="{hbBoxEnd:index === hBarr.length-1}">
        <div class="hbLeft ">
          <p><dome></dome>{{item.amount%1===0?item.amount:item.amount-item.amount%1}}<dome>{{item.amount%1===0?'.0':'.'+item.amount%1*10}}</dome></p>
          <p>{{item.description_map.sum_condition}}</p>
        </div>
        <div class="hbRight">
          <p class="right">{{item.description_map.validity_delta}}</p>
          <p>{{item.name}}</p>
          <p>{{item.end_date}}</p>
          <p>{{item.description_map.phone}}</p>
        </div>
      </div>
      <div class="hbButton"><p>:</p></div>
      <div class="lastHb">
        <span @click="isIf = hbDes">
          <router-link :to="{name: 'hbHistory', query: {move:hbHis}}">></router-link>
        </span>
      </div>
      <div class="box navbar-fixed-bottom">
        <button class="btnExchange btn btn-default" @click="goExchange"></button>
        <button class="btnCommend btn btn-default" @click="goCommend"></button>
      </div>
    </div>
    <router-view></router-view>
    <div v-if="isIf === ''" class="chitInterface">
      <span @click="isIf = false">
        <router-link :to="{name: 'coupon'}" class="right"></router-link>
      </span>
      <img src="../../images/description.png" class="imgQuestionMark right">
      <div class="chitContent">
        <img src="../../images/voucher.png" class="voucher" alt="">
        <p></p>
        <p></p>
        <button class="btn btn-success" @click="goDownLoad"></button>
      </div>
      <router-view></router-view>
    </div>
  </section>
</template>

<script>
// 
export default {
  name: "benefit",
  data () {
    return {
      isIf: '',
      id: false,
      hBarr:[],
      hbDes: '',
      hbHis: '',
      hbExchange: '',
      hbCommend: ''
    }
  },
  methods: {
    // 
    goDownLoad () {
      this.$router.push({name:'download',params:{move:''}})
    },
    // 
    goExchange () {
      this.isIf = ''
      this.$router.push({name:'exchange',params:{move:''}})
    },
    // 
    goCommend () {
      this.isIf = ''
      this.$router.push({name:'commend',params:{move:''}})
    }
  },
  computed: {
    getID () {
      return this.$store.state.ghc.userInfo
    }
  },
  watch: {
    $route(now,lod){
      console.log('now',now)
      console.log('lod',lod)
      if (now.name === 'benefit') {
        this.isIf = ''
        console.log(this.isIf)
      }
    },
    getID: {
      //,
      handler(){
        this.id = this.$store.state.ghc.userInfo.id
        console.log('')
      },
      //,false
      immediate:true,
      //
      deep:true
}
  },
  beforeRouteEnter (to, from, next) {
    console.log('from',to)
    if (to.query.id) {
      next()
    }
  },
  mounted () {
    // 
    console.log('id',this.id);
    //
    this.$store.commit({
      type:'amendDataLoad'
    });
    this.$http({
      method: 'get',
      url: `https://elm.cangdu.org/promotion/v2/users/${this.id}/hongbaos`,
      // withCredentials: true, // false
      params:{
        limit:20,
        offset:0
      }
    }).then((res) => {
      this.$store.commit({
        type:'amendDataLoad'
      });
      console.log('tap', res);
      this.hBarr = res.data
    });
  }
}
</script>

<style scoped>
  section {
    background-color: #f5f5f5;
  }
  .box {
    width: 100%;
    background-color: white;
    height: 0.5rem;
    display: flex;
    /*line-height: 0.4rem;*/
  }
  .btnExchange {
    width: 50%;
  }
  .btnCommend {
    width: 50%;
  }
  .hB {
    width: 50%;
    text-align: center;
    line-height: 0.6rem;
  }
  .spanColor {
    color: #90B4FC;
  }
  .router-link-active {
    color: #e8290c;
  }
  .chit {
    width: 50%;
    text-align: center;
    line-height: 0.6rem;
  }
  .imgQuestionMark {
    width: .16rem;
    margin-top: 0.02rem;
  }
  .chitInterface {
    margin-top: 0.2rem;
    width: 100%;
    height: 100%;
    position: fixed;
  }
  .chitContent {
    text-align: center;
    margin-top: 1.5rem;
  }
  .voucher {
    width: 1rem;
    margin-bottom: 0.1rem;
  }
  .hbBox {
    display: flex;
    width: 96%;
    background-color: white;
    border-radius: 5%;
    margin: 0.1rem auto 0;
    height: 1rem;
  }
  .hbDiv {
    margin: 0.1rem;
  }
  .hbLeft {
    width: 30%;
    box-sizing: border-box;
    margin: 5% 0;
    border-right: #a4a4a4 0.01rem dotted;
    /*line-height: 0.5rem;*/
    text-align: center;
  }
  .hbLeft>p:nth-child(1) {
    color: red;
    font-size: 0.3rem;
  }
  .hbLeft>p {
    color: #a4a4a4;
    font-size: 0.12rem;
  }
  .hbRight {
    width: 70%
  }
  .hbRight>p {
    text-indent: 0.30rem;
  }
  .hbRight>p:nth-child(1) {
    margin: 0.2rem 0.2rem 0 0 ;
    color: red;
    font-size: 0.16rem;
  }
  .hbRight>p:nth-child(2) {
    font-size: 0.16rem;
    margin-top: 0.2rem;
    color: black;
  }
  .hbRight>p {
    font-size: 0.12rem;
    color: #a4a4a4;
  }
  .hbButton {
    margin: 0 auto 0 ;
    width: 96%;
    background-color: #fafafa;
    color: #a4a4a4;
    font-size: 0.12rem;
    text-indent: 0.10rem;
    border-radius: 0 0 40% 40%;
  }
  .hbBoxEnd {
    border-radius: 5% 5% 0 0;
  }
  p {
    margin: 0;
  }
  .lastHb {
    width: 100%;
    margin: 0.2rem auto 0;
    text-align: center;
  }
  .lastHb a {
    color: #bbbbbb;
  }
  .pitchOn {
    color: #008de1;
  }
  .domeHb {
    position: absolute;
    display: inline-block;
    top: 0.85rem;
    height: 0.03rem;
    left: 0.79rem;
    width: 0.3rem;
    background-color: #008de1;
  }
  .domeVoucher {
    display: inline-block;
    width: 0.57rem;
    position: absolute;
    top: 0.85rem;
    left: 2.53rem;
    height: 0.03rem;
    background-color: #008de1;
  }
  dome {
    font-size: 0.16rem;
    color: red;
  }
</style>



