  position: fixed;
  width: 100%;
  z-index: 222;
}
/**/
.dome-top {
  display: flex;
  width: 100%;
  padding-top: 0.2rem;
  background-color: white;
  border-bottom: #e4e4e4 solid 0.01rem;
  background-color: white;
  z-index: 333;
}
.dome-top>div {
  width:30%;
  text-align: center;
  margin: 0 auto 0.1rem ;
  background-color: white;
  z-index: 444;
}
.dome-top>div:nth-child(-n+2) {
  border-right: #e4e4e4 solid 0.01rem;
}
/**/
.dome-spinner {
  position: fixed;
  width: 100%;
  /*height: 100%;*/
  top: 0.9rem;
  /*z-index: 3;*/
  /*background-color: rgba(22,22,22,0.1)*/
}
/**/
.dome-classify {
  position: absolute;
  width: 100%;
  /*height: 100%;*/
  height: 4.5rem;
  display: flex;
  z-index: 9;
  top: 0;
  /*background-color: rgba(22,22,22,0.1);*/
}
/**/
.dome-classifyLeft {
  width: 50%;
  background-color: #eeeeee;
  height: 100%;
}
/**/
.dome-classifyLeft-line {
  line-height: 0.5rem;
  text-indent: 0.2rem;
}
/**/
