  border-bottom: #f5f5f5 solid 0.01rem;
}
/*span*/
.dome-classify-right-line-span {
  height: 0.5rem;
  line-height: 0.5rem;
  margin-right: 0.1rem;
}
/**/
.domeClassifyFocus {
  background-color: white;
}
/**/
.dome-sort {
  position: absolute;
  width: 100%;
  height: 3rem;
  background-color: white;
  z-index: 9;
  top:0;
}
/**/
.dome-sort-line {
  height: 0.5rem;
  width: 100%;
  line-height: 0.5rem;
  display: flex;
}
/**/
.dome-sort-icon-bag {
  width: 10%;
  height: 0.5rem;
  line-height: 0.5rem;
  text-align: center;
}
/**/
.dome-sort-way {
  width: 90%;
  height: 0.5rem;
  line-height: 0.5rem;
  border-bottom: #a4a4a4 solid 0.01rem;
  top:0;
  z-index: 9;
}
/**/
.dome-sort-true {
  position: absolute;
  right: 0;
  width: 5%;
  height: 0.5rem;
  line-height: 0.5rem;
  color: #008de1;
}
/**/
.domeIcon1 {
  color: #008de1;
}
.domeIcon2 {
  color: #90B4FC;
}
.domeIcon3 {
  color: red;
}
.domeIcon4 {
  color: #ffed45;
}
.domeIcon5 {
  color: #42ff9a;
}
.domeIcon6 {
  color: #fcc934;
}
/**/
.dome-screen {
  position: absolute;
  width: 100%;
  height: 2.5rem;
  background-color: #a4a4a4;
  z-index: 9;
  top: 0;
}
/**/
.dome-screen-top {
  width: 100%;
  height: 2rem;
  background-color: white;
}
/**/
.dome-screen-distribution {
  height: 0.8rem;
  width: 90%;
  margin: 0 auto 0;
  line-height: 0.3rem;
}
/*p*/
.dome-screen-distribution>p+p {
  height: 0.3rem;
  line-height: 0.3rem;
  width: 30%;
  border: #a4a4a4 solid 0.01rem;
  border-radius: 0.05rem;
  text-indent: 0.1rem;
}
/**/
.dome-screen-property {
  height: 1.2rem;
  width: 90%;
  margin: 0 auto 0;
  /*line-height: 0.3rem;*/
}
/*P*/
.dome-screen-property-p {
  height: 0.7rem;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}
/*p*/
.dome-screen-property-p p {
  margin: 0 auto 0 0;
  height: 0.3rem;
  line-height: 0.3rem;
  width: 30%;
  border: #a4a4a4 solid 0.01rem;
  border-radius: 0.05rem;
  text-indent: 0.1rem;
}
/*i*/
.dome-screen-top i {
  font-size: 0.2rem;
  color: #008de1;
}
/*i*/
.dome-imitate {
  font-size: 0.14rem;
  border-radius: 0.03rem;
  padding: 0.02rem;
}
/*i1*/
.dome-imitate-color1 {
  border: #2fd5cf solid 0.01rem;
  color: #2fd5cf;
}
/*i2*/
.dome-imitate-color2 {
  border: #a4a4a4 solid 0.01rem;
  color: #a4a4a4;
}
/*i3*/
.dome-imitate-color3 {
  border: #3966d5 solid 0.01rem;
  color: #3966d5;
}
/*i4*/
.dome-imitate-color4 {
  border: #efa93d solid 0.01rem;
  color: #efa93d;
}
/*i5*/
.dome-imitate-color5 {
  border: #ef7839 solid 0.01rem;
  color: #ef7839;
}
/*i6*/
.dome-imitate-color6 {
  border: #a4a4a4 solid 0.01rem;
  color: #a4a4a4;
}
/**/
.dome-screen-below {
  width: 100%;
  height: 0.5rem;
  background-color: #98fcdf;
  display: flex;
}
/**/
.dome-screen-but-left{
  width: 50%;
  height: 0.5rem;
  background-color: #f5f5f5;
  line-height: 0.5rem;
  text-align: center;
}
/**/
.dome-screen-but-right {
  width: 50%;
  height: 0.5rem;
  background-color: #f5f5f5;
  line-height: 0.5rem;
  text-align: center;
}
/**/
.dome-screen-below button {
  width: 90%;
}
/**/
.dome-shade {
  width: 100%;
  height: 100%;
