<template>
  <section class="container-fluid confirmOrder">
    <div class="deliveryAddres">
      <router-link :to="{name:'chooseAddress'}">
        <div class="addressLeft">
          <i class="iconfont">&#xe636;</i>
          <div class="personal">
            <span>{{selectAds.name}}</span>
            <span>{{selectAds.sex === 1?'':''}}</span>
            <span>{{selectAds.phone}}</span><br>
            <span :class="{colorRed:selectAds.tag === ''||'',colorBlue:selectAds.tag === '',colorGreen:selectAds.tag === ''}">{{selectAds.tag}}</span>
            <span>{{selectAds.address}}</span>
          </div>
        </div>
        <div class="addressRight">
          <i class="iconfont">&#xe634;</i>
        </div>
      </router-link>
    </div>
    <div class="arriveTime">
      <p></p>
      <div>
        <span></span>
        <span>|</span>
        <span>21:10</span>
        <p></p>
      </div>
    </div>
    <div class="payStyle">
      <router-link :to="{}" @click.native="payStyle">
        <span></span>
        <span>{{''}} <i class="iconfont">&#xe634;</i> </span>
      </router-link>
      <div><span></span><span>APP</span></div>
    </div>
    <div class="orderInfo"  v-for="(item,key) in cartGoods">
      <div>
        <img src="../../assets/logo.png"/>
        <span>{{''}}</span>
      </div>
      <ul>
        <el-row :gutter="10" >
          <el-col :xs="16"><div class="grid-content bg-purple">{{item.entities.name}}</div></el-col>
          <el-col :xs="4"><div class="grid-content bg-purple-light special">x{{item.quantity}}</div></el-col>
          <el-col :xs="4"><div class="grid-content bg-purple-light"> {{item.entities.price}}</div></el-col>
        </el-row>
        <el-row :gutter="10">
          <el-col :xs="12"><div class="grid-content bg-purple"></div></el-col>
          <el-col :xs="12"><div class="grid-content bg-purple-light"> {{item.entities.packing_fee*item.quantity}}</div></el-col>
        </el-row>
        <el-row :gutter="10">
          <el-col :xs="12"><div class="grid-content bg-purple"></div></el-col>
          <el-col :xs="12"><div class="grid-content bg-purple-light"> {{item.entities.packing_fee*item.quantity}}</div></el-col>
        </el-row>
      </ul>
      <div class="pay">
        <el-row :gutter="10">
          <el-col :xs="12"><div class="grid-content bg-purple">   {{item.quantity*item.entities.price+item.entities.packing_fee*item.quantity*2}}</div></el-col>
          <el-col :xs="12"><div class="grid-content bg-purple-light special"></div></el-col>
        </el-row>
        <el-row :gutter="10">
          <el-col :xs="24"><div class="grid-content bg-purple-light special"> {{item.quantity*item.entities.price+item.entities.packing_fee*item.quantity*2}}</div></el-col>
        </el-row>
      </div>
    </div>
    <div class="remarks">
      <router-link :to="{name:'remark'}">
        <span></span>
        <span>{{''}}<i class="iconfont">&#xe634;</i> </span>
      </router-link>
      <router-link :to="{name:'invoice'}">
        <span></span>
        <span>{{''}}<i class="iconfont">&#xe634;</i></span>
      </router-link>
    </div>
    <div class="foot">
      <span> {{totalPrices}} </span>
      <button @click="confirm"></button>
    </div>
    <!--actionSheet-->
    <mt-actionsheet :actions="actions" cancelText ='' closeOnClickModal=true v-model="sheetVisible" class="actionSheet">
    </mt-actionsheet>
  </section>
</template>

<script>
  import Vue from 'vue'
  import { Actionsheet } from 'mint-ui';
  Vue.component(Actionsheet.name, Actionsheet);
// 
export default {
  name: "confirmOrder",
  data(){
    return{
      // 
      selectAds:{},
      // 
      thisAdrs:{},
      // 
      cartGoods:[],
      // 
      totalPrices: '',
      // actionSheet
      sheetVisible:false,
      //actionSheet
      actions:[
        {
        name:'',
      },
        {
          name:'()',
        },
        {
          name:'',
        }
      ]
      }
    },

  methods:{
    // 
    payStyle(){
      this.sheetVisible = true
    },
    confirm(){
      // 
      this.$router.push({name:'payment'})
    }
  },
  created(){
// 
    this.cartGoods = this.$store.state.dome.cartSingleFood;
    console.log(this.cartGoods);
    // 
    this.totalPrices = 0;
    for (let prices of this.cartGoods) {
      this.totalPrices += prices.entities.price * prices.quantity+prices.entities.packing_fee*prices.quantity*2
    }
  },
  mounted(){
    // 
    this.thisAdrs = this.storage.get("thisAdrs");
    // 
    this.selectAds = this.$store.state.ghc.useThisAds?this.$store.state.ghc.useThisAds:this.thisAdrs;
    console.log(this.selectAds)
  },
  beforeRouteEnter(to,from,next){
    next(vm=>{
      // footer
      vm.$store.state.ghc.showOrHidden = false
    })
  },
  beforeRouteLeave(to,from,next){
    next(
      // footer
      this.$store.state.ghc.showOrHidden = true
    )
  },
}
</script>

<style scoped>
.confirmOrder{
  width: 100%;
  padding: 0;
  background-color: whitesmoke;
  list-style: none;
}
.confirmOrder>div{
  padding: 0 0.16rem;
  width: 100%;
}
  .deliveryAddres{
    display: inline-block;
    width: 100%;
    height: 1rem;
    border-bottom: 0.02rem dashed orange;
    padding: 0 0.1rem;
  }
.deliveryAddres>a{
  width: 100%;
  text-decoration: none;
  color: #000;
}
  .addressLeft{
    width: 95%;
    float: left;
    padding: 0.3rem 0;
  }
.addressLeft>i{
  display: inline-block;
  color: #008de1;
  font-size: x-large;
}
.addressLeft>div{
  display: inline-block;
}
.addressLeft>div>span{
  margin: 0 0.02rem;
  display: inline-block;
}
.addressLeft>div>span:nth-child(1){
  font-size: 0.16rem;
  font-weight: bolder;
}
.addressLeft>div>span:nth-child(5){
  width: 0.6rem;
  height: 0.2rem;
  background-color: limegreen;
  border-radius: 0.04rem;
  color: white;
  font-size: 0.12rem;
  text-align: center;
  line-height: 0.2rem;
}
.addressLeft>div>span:nth-child(6){
  overflow: no-display;
}
.deliveryAddres .addressLeft>div>.colorRed{
  background-color: red;
}
.deliveryAddres .addressLeft>div>.colorBlue{
  background-color: #008de1;
}
.deliveryAddres .addressLeft>div>.colorGreen{
  background-color: limegreen;
}
.addressLeft>div>span:nth-child(6){
  color: darkgrey;
  font-size: 0.14rem;
}
  .addressRight{
    float: right;
    line-height: 1rem;
  }
  .arriveTime{
    margin-top: 0.04rem;
    height: 0.8rem;
    background-color: white;
    border-left: 0.06rem solid #008de1;
    display: inline-block;
  }
.arriveTime>p{
  display: inline-block;
  float: left;
  line-height: 0.8rem;
  font-size: 0.16rem;
  font-weight: bolder;
}
.arriveTime>div{
  display: inline-block;
  float: right;
  position: relative;
  padding: 0.14rem 0;
}
.arriveTime>div>span{
  font-size: 0.14rem;
  color: #008de1;
  margin: 0 0.02rem;
}
.arriveTime>div>p{
  font-size: 0.12rem;
  color: #fff;
  background-color: #008de1;
  border-radius: 0.04rem;
  width: 0.6rem;
  text-align: center;
  margin-bottom: 0;
  position: absolute;
  right: 0;
  top: 0.4rem;
}
  .payStyle{
    margin-top: 0.04rem;
    height: 0.8rem;
    background-color: white;
  }
.payStyle>a{
  height: 0.4rem;
  line-height: 0.4rem;
  text-decoration: none;
  display: inline-block;
  width: 100%;
  border-bottom: 0.01rem solid #e4e4e4;
}
.payStyle>a>span{
  color: grey;
  font-size: 0.14rem;
  float: left;
}
.payStyle>a>span+span{
  color: lightgray;
  font-size: 0.12rem;
  float: right;
}
.payStyle>div{
  height: 0.3rem;
  line-height: 0.3rem;
  width: 100%;
  display: inline-block;
}
.payStyle>div>span{
  color: lightgray;
  font-size: 0.12rem;
  float: left;
}
.payStyle>div>span+span{
  float: right;
}
.confirmOrder>.orderInfo{
  margin-top: 0.1rem;
  background-color: white;
  padding: 0;
}
.orderInfo>div:nth-child(1){
  padding: 0 0.16rem;
  height: 0.6rem;
  line-height: 0.6rem;
  text-decoration: none;
  display: inline-block;
  width: 100%;
  border-bottom: 0.01rem solid #e4e4e4;
}
.orderInfo>div:nth-child(1)>img{
  width: 0.2rem;
  height: 0.2rem;
  background-color: #008de1;
}
.orderInfo>div:nth-child(1)>span{
  font-size: 0.16rem;
  padding: 0 0.1rem;
}
.orderInfo>ul{
  padding: 0 0.16rem;
  border-bottom: 0.01rem solid #e4e4e4;
}
.orderInfo>div:nth-child(3){
  padding: 0 0.16rem;
  border-bottom: 0.01rem solid #e4e4e4;
}
.orderInfo>.pay{
  padding: 0 0.16rem;
}
.bg-purple {
  text-align: left;
}
.bg-purple-light {
  text-align: right;
}
.grid-content {
  min-height: 0.4rem;
  margin: 0.02rem 0;
  line-height: 0.4rem;
  color: grey;
}
  .special{
    color: orangered;
  }
  .pay{
    border-bottom:none;
  }
.remarks{
  margin-top: 0.1rem;
  height: 0.9rem;
  background-color: white;
  margin-bottom: 0.7rem;
}
.remarks>a{
  height: 0.4rem;
  line-height: 0.4rem;
  text-decoration: none;
  display: inline-block;
  width: 100%;
  border-bottom: 0.01rem solid #e4e4e4;
}
.remarks>a>span{
  color: grey;
  font-size: 0.14rem;
  float: left;
}
.remarks>a>span+span{
  float: right;
  color: lightgray;
}
.remarks>a:last-child{
  border-bottom: none;
}
.confirmOrder>.foot{
    padding: 0;
    width:100%;
    height: 0.5rem;
    background-color: #000;
    line-height: 0.5rem;
    color: #fff;
    font-size: 0.16rem;
    position: fixed;
    left: 0;
    bottom: 0;
  }
.confirmOrder>.foot>span{
  padding: 0 0.16rem;
}
.confirmOrder>.foot>button{
  display: inline-block;
  float: right;
  background-color: limegreen;
  outline-style: none;
  border: none;
  width: 1rem;
}
.mint-actionsheet{
  width: 100%;
  padding: 0;
  height: 2rem;
  background-color: white;
}

</style>



