<template>
<section class="container-fluid search">
  <div class="searchArea">
    <!--:
    v-model,;
    @focus ;
    @click 
    -->
    <el-input
      v-model="input"
      @focus="showIcon"
      placeholder=""
      ><i slot="suffix" class="el-input__icon el-icon-close" @click="clearInput" v-if="isIcon"></i></el-input>
    <el-button
      type="primary"
      @click="searchInfo(input)" :disabled="!input"></el-button>
  </div>
  <ul class="recordList" v-if="record">
    <li></li>
    <li v-for = "(item ,key) in list" class="records" @click.self="searchInRecord(item)">{{item}}<i class="iconfont" @click.self="removeData(key)">&#xe62a;</i></li>
    <li @click="clearAllRecord"></li>
  </ul>
  <ul class="searchFail" v-if="noAnswer">
    <li>!</li>
  </ul>
  <ul class="searchFail" v-if="isAnswer">
    <li v-for="(item ,key) in arrData">
      <p>{{item.name}}</p>
      <p>{{item.description}}</p>
    </li>
  </ul>
</section>
</template>

<script>
// 
import Vue from 'vue'
export default {
  name: "search",
  data() {
    return {
      // 
      isIcon:false,
      // 
      noAnswer:false,
      // ,
      isAnswer:false,
      // ,
      record:true,
      input: '',
      // 
      list:[],
      // store
      curCity:{},
      // 
      arrData:[],
      // 
      geohash:'',
      // 
      userInfomation:{}
    }
  },
  methods: {
    //  ,
    clearInput(){
      this.input = "";
      this.isIcon = false;
    },
    // ,
    showIcon(){
      this.isIcon = true
    },
    // ,
    clearAllRecord(){
      this.storage.set("list", []);
      this.record = false
    },
    // ,
    removeData(key){
      this.list.splice(key,1);
      this.storage.set("list",this.list);
      this.list.checked = false;
    },
    // 
    searchInRecord(i){
      this.searchInfo(i)
    },
    searchInfo(i){
      //
      this.input = i;
      // 
      this.doAdd(i);
      //
      this.$store.commit({
        type:'amendDataLoad'
      });
      // ,,
      Vue.axios.get(`https://elm.cangdu.org/v4/restaurants?geohash=${this.curCity.latitude},${this.curCity.longitude}&keyword=${i}`,null).then(res => {
        //
        this.$store.commit({
          type:'amendDataLoad'
        });
        // 
        this.isAnswer = true;
        this.record = false;
        this.noAnswer = false;
        console.log(res.data);
        this.arrData = res.data;
        // 
        if(res.data === []){
          this.record = false;
          this.noAnswer = true;
          this.isAnswer = false
        }
      }).catch((error) => {
        console.log(error);
        // ,
        this.record = false;
        this.noAnswer = true;
        this.isAnswer = false
      })
    },
    doAdd() {
       // 
        this.input = this.input.trim();
        // list
        if (this.list.length>0){//
          // 
          if(this.list.includes(this.input)){
            // ,
            this.list.splice(this.list.indexOf(this.input),1,this.input);
          }else{
            // ,
            // this.list.unshift(this.input)
            this.list.unshift(this.input);
          }
        }else{
          // ,
          this.list.unshift(this.input);
        }

      // 6,
      if(this.list.length>7){
        this.list.pop()
      }
      this.storage.set("list", this.list)
    },

  },
  mounted(){
    /* vue*/
    let list = this.storage.get("list");
    if (list){
     this.list = list;
    };
    // vuex,id,geohash
    this.userInfomation = this.$store.state.ghc.userInfo;
    this.geohash = this.$store.state.ghc.localInfo.geohash;
    console.log(this.userInfomation,this.geohash);
  },
  beforeRouteEnter(to,from,next){
    next(vm=>{
      vm.curCity = vm.$store.state.ghc.currentCity
    })
  }
}
</script>

<style scoped>
  .search{
    width: 100%;
    height: 100%;
    padding: 0;
  }
  .searchArea{
    display: inline-block;
    width: 100%;
    height: 0.6rem;
    line-height: 0.6rem;
    background-color: white;
    padding: 0 0.1rem;
  }
  .el-input{
    width: 2.7rem;
    float: left;
  }
  .el-input__inner{
    outline-style: none;
  }
  .el-row{
    width: 0.8rem;
  }
  .el-button--primary{
    width: 0.8rem;
    float: right;
    margin-top: 0.1rem;
  }
  .recordList{
    background-color: white;
    margin-bottom: 0;
  }
  .recordList>li{
    border-bottom: 0.01rem solid #e4e4e4;
    height: 0.4rem;
    line-height: 0.4rem;
    padding: 0 0.1rem;
  }
  .recordList>li:nth-child(1){
    background-color: #f5f5f5;
  }
  .recordList>li:last-child{
    text-align: center;
    color: #008de1;
    border-bottom: none;
  }
  .records>i{
    float: right;
  }
  .searchFail>li{
    text-align: left;
    padding: 0 0.1rem;
    color: #000;
    height: 0.6rem;
    background-color: white;
    margin-top: 0.02rem;
  }
  .searchFail>li>p{
    height: 0.2rem;
  }
</style>



