<template>
  <section class="container-fluid addAddress">
    <div class="content">
      <el-row :gutter="10">
        <el-col :xs="8"><div class="grid-content bg-purple"><span></span></div></el-col>
        <el-col :xs="16"><div class="grid-content bg-purple-light"><input type="text" placeholder="" v-model="yourName"></div></el-col>
      </el-row>
      <el-row :gutter="10">
        <el-col :xs="8"><div class="grid-content bg-purple"></div></el-col>
        <el-col :xs="16">
          <div class="grid-content bg-purple-light">
            <el-radio v-model="checked" label="1"></el-radio>
            <el-radio v-model="checked" label="2"></el-radio>
          </div>
        </el-col>
      </el-row>
      <el-row :gutter="10">
        <el-col :xs="8"><div class="grid-content bg-purple"><span></span></div></el-col>
        <el-col :xs="14"><div class="grid-content bg-purple-light"><input type="text" placeholder="" v-model="yourNumber"></div></el-col>
        <el-col :xs="2"><div class="grid-content bg-purple "><i class="iconfont add" @click="addPhone_bk">&#xe601;</i></div></el-col>
      </el-row>
      <el-row :gutter="10" v-if="addPhone">
        <el-col :xs="8"><div class="grid-content bg-purple"></div></el-col>
        <el-col :xs="16">
          <div class="grid-content bg-purple-light">
            <input type="text" placeholder = "" v-model="phone_bk">
          </div>
        </el-col>
      </el-row>
      <el-row :gutter="10">
        <el-col :xs="8"><div class="grid-content bg-purple"><span></span></div></el-col>
        <el-col :xs="16"><div class="grid-content bg-purple-light"><input type="text" placeholder="//" @click="searchAds" v-model="yourAddres"></div></el-col>
      </el-row>
      <el-row :gutter="10">
        <el-col :xs="8"><div class="grid-content bg-purple"></div></el-col>
        <el-col :xs="16"><div class="grid-content bg-purple-light"><input type="text" placeholder="()" v-model="yourDetailAddres"></div></el-col>
      </el-row>
      <el-row :gutter="10">
        <el-col :xs="8"><div class="grid-content bg-purple"><span></span></div></el-col>
        <el-col :xs="16"><div class="grid-content bg-purple-light"><input type="text" placeholder="///" v-model="yourTag"></div></el-col>
      </el-row>
    </div>
    <button @click="addAddress"></button>
  </section>
</template>

<script>
  import Vue from 'vue'
// 
export default {
  name: "addAddress",
  data(){
    return {
      // 
      userInfomation:{},
      //
      checked:1,
      // name
      yourName:'',
      // 
      yourNumber:'',
      // 
      phone_bk:'',
      // 
      addPhone:false,
      // 
      yourAddres:'',
      // 
      yourDetailAddres:'',
      // 
      yourTag:'',
      // 
      geohash:'',
      // 
      newAddres:[]
    }
  },
  methods:{
    // 
    searchAds(){
      this.$router.push({name:'searchAddress'})
    },
    // 
    addPhone_bk(){
      this.addPhone = true
    },
    // ,vuex,
    addAddress(){
      // ,
      if(this.yourNumber === ''|| this.yourName === ''||this.yourDetailAddres === ''){
        alert('');
        return
      }
      switch (this.yourTag) {
        case '':
          this.yourTag = '';
          break;
        case '':
          this.yourTag = '';
          break;
        case '':
          this.yourTag = '';
          break;
        case '':
          this.yourTag = '';
          break;
      }
      //,
      this.$http({
        method:'post',
        url:`https://elm.cangdu.org/v1/users/${this.userInfomation.user_id}/addresses`,
        withCredentials:true,
        data:{
          address: this.yourAddres,
          address_detail: this.yourDetailAddres,
          geohash: this.geohash,
          name: this.yourName,
          phone: this.yourNumber,
          tag: this.yourTag,
          sex: this.checked,
          poi_type:0,
          phone_bk:'',
          tag_type:2
        }
      }).then(res => {
        // chooseAddress
        this.$router.push({name:'chooseAddress'});
        console.log('',res.data)
      }).catch((error) => {
        console.log(error);
      });
    }
  },
  created(){
    // 
    this.yourAddres = this.$route.params.select;
    // vuex,id,geohash
    this.userInfomation = this.$store.state.ghc.userInfo;
    this.geohash = this.$store.state.ghc.localInfo.geohash;
    console.log(this.userInfomation,this.geohash);
  }
}
</script>

<style scoped>
.addAddress{
  width: 100%;
  padding: 0;
  background-color: #f5f5f5;
}
.content{
  padding: 0 0.16rem;
  margin-top: 0.1rem;
  background-color: white;
}
.addAddress>button{
  width: 90%;
  height: 0.4rem;
  margin: 0.1rem 5%;
  background-color: limegreen;
  color: #ffffff;
  border: none;
  border-radius: 0.04rem;
  text-align: center;
}
.grid-content{
  min-height: 0.4rem;
  margin: 0.02rem 0;
  line-height: 0.4rem;
}
.bg-purple {
  text-align: left;
}
.bg-purple-light {
  text-align: left;
  color: grey;
  border-bottom: 0.01rem solid #e4e4e4;
}
.grid-content>input{
  outline-style: none;
}
.add{
  color: #008de1;
  font-size: 0.2rem;
}
</style>



